const tabs = () => {
	const tabLinks   = document.querySelectorAll('.tabs__button');
	const tabContent = document.querySelectorAll('.tabs__content');
	const optionMenu = document?.querySelector('.tabs__head');
	const selectBtn  = optionMenu?.querySelector('.tabs__text');
	const options    = optionMenu?.querySelectorAll('.tabs__item');
	const selected   = optionMenu?.querySelector('.tabs__selected');

	function openTabs(el) {
		const btnTarget = el.currentTarget;
		const { country } = btnTarget.dataset;

		tabContent.forEach((el) => {
			el.classList.remove('active');
		});

		tabLinks.forEach((el) => {
			el.classList.remove('active');
		});

		document.querySelector(`#${country}`).classList.add('active');

		btnTarget.classList.add('active');
	}

	tabLinks.forEach((el) => {
		el.addEventListener('click', openTabs);
	});

	if (selectBtn) {
		selectBtn.addEventListener('click', () => optionMenu.classList.toggle('active'));
		options.forEach((option) => {
			option.addEventListener('click', () => {
				const selectedOption = option.querySelector('.tabs__button').innerHTML;
				selected.innerHTML   = selectedOption;
				optionMenu.classList.remove('active');
			});
		});
	}
};
export default tabs;
